import React from 'react'
// import { Link, Switch, Route } from 'react-router-dom'
// import FeatherIcons from 'feather-icons-react'
import Chart from "chart.js"
import axios from 'axios'
export default class AdminAccountAnalysis extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            dashboardValue: {
                labels: [],
                data: []
            },
            control: false,
            dashColor: "#ffffff",
            displayType: 0,
            dashBoardType: 0
        }
        this.SelectRefs = React.createRef()

        this.initDashboardValue = this.initDashboardValue.bind(this)
        this.exportDashBoard = this.exportDashBoard.bind(this)
        this.changeDashStyle = this.changeDashStyle.bind(this)
        this.changeDisplayState = this.changeDisplayState.bind(this)
        this.initMyChart = this.initMyChart.bind(this)
        this.renderValue = [
            ["This week", "7 days"],
            ["This month", "30 days"]
        ]
    }

    initMyChart() {
        this.myChart = new Chart(document.getElementById('myChart'), {
            type: 'line',
            data: {
                labels: [],
                datasets: [{
                    data: [],
                    lineTension: 0,
                    backgroundColor: 'transparent',
                    borderColor: '#007bff',
                    borderWidth: 4,
                    pointBackgroundColor: '#007bff',
                    borderDash: [20, 0.5],
                }]
            },
            options: {
                responsive: true,
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: false,
                            fontSize: 13
                        }
                    }],
                    xAxes: [{
                        ticks: {
                            beginAtZero: false,
                            fontSize: 13
                        }
                    }]
                },
                legend: {
                    display: false
                },
                title: {
                    display: true,
                    fontSize: 16,
                    text: "Account analysis"
                },
            }
        });
    }

    componentDidMount() {
        this.initMyChart()
        this.initDashboardValue()
    }

    async initDashboardValue() {
        if (this.myChart !== null) this.myChart.reset()
        let svalue = await axios.post('/admin/get-account-development-dashboard', {
            dashboardType: this.SelectRefs.current.value
            , displayType: this.state.displayType
        })
        this.setState({ dashBoardType: this.SelectRefs.current.value })
        this.drawDashboard(svalue.data)
    }

    exportDashBoard() {
        let img = document.createElement('a');
        img.setAttribute('href', this.myChart.toBase64Image())
        img.setAttribute('download', "Account Dash Board")
        document.body.appendChild(img)
        img.click()
        img.remove()
    }

    changeDashStyle() {
    }

    drawDashboard(svalue) {
        this.myChart.data.labels = svalue.labels
        this.myChart.data.datasets[0].data = svalue.data
        this.myChart.update();
    }

    async changeDisplayState() {
        this.setState({ displayType: this.state.displayType ^ 1 })
        let svalue = await axios.post('/admin/get-account-development-dashboard', {
            dashboardType: this.state.dashBoardType
            , displayType: this.state.displayType ^ 1
        })
        this.drawDashboard(svalue.data)
    }

    render() {

        return (

            <div>
                <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                    <h1 className="h2">Chart</h1>
                    <div className="btn-toolbar mb-2 mb-md-0">
                        <div className="btn-group mr-2">
                            <button type="button" className="btn btn-sm btn-outline-secondary" onClick={this.changeDashStyle} >Dark</button>
                            <button type="button" className="btn btn-sm btn-outline-secondary" onClick={this.exportDashBoard} >Export</button>
                        </div>
                        <div className="btn-group mr-2">
                            <button type="button" className="btn btn-sm btn-outline-secondary" onClick={this.changeDisplayState} >
                                {this.renderValue[this.state.dashBoardType][this.state.displayType]}
                            </button>
                            <select onChange={this.initDashboardValue} ref={this.SelectRefs} defaultValue={0} className="btn btn-sm btn-outline-secondary">
                                <option value={0}>Week</option>
                                <option value={1}>Month</option>
                                <option value={2}>Year</option>
                            </select>
                        </div>
                    </div>
                </div>
                <canvas className="my-4 w-100" id="myChart" width={900} height={380} />
            </div>
        )
    }
}