import React from 'react'
// import $ from 'jquery'
import {Link, Switch, Route} from 'react-router-dom'
import "./admin-frame.css"
import ADMAnalysis from "./admin-sys-analysis/admin-sys-analysis.js"
import ADMAccountMan from './admin-account-man/admin-acc-mana'
import 'bootstrap/dist/js/bootstrap.min.js'
import axios from 'axios'

export default class AdminFrame extends React.Component {
    constructor(props){
        super(props)
        this.createLinkDirect = this.createLinkDirect.bind(this)
        this.defaultDirectClass = "dropdown-item"
        this.defaultDirectSize= 3
        this.preLinkDirect = null
        this.state = {linkDirect: this.createLinkDirect()}

    }
    isAdmLogin(){
        axios.post("/auth/admin-is-loggin").then(res=>{
            if(!res.data){
                window.location.replace('/')
            }
        })
    }

    createLinkDirect(){
        let slinkDirect = []
        for(let x = 0; x < this.defaultDirectSize; x++){
            slinkDirect.push(this.defaultDirectClass)
        }
        return slinkDirect
    }



    onDirectClick(index){
        let tempData = this.state.linkDirect
        if(this.preLinkDirect != null ){
            tempData[this.preLinkDirect] = this.defaultDirectClass
        }
        tempData[index] = this.defaultDirectClass+ " active"
        this.preLinkDirect = index
        this.setState({linkDirect: tempData})

    }

    componentDidMount(){
        this.isAdmLogin()
    }

    logoutRequest(){
        axios.post('/logout-request').then(result=>{
            if(result) window.location.reload('/')
        })
    }

    render() {
        return (
            <div>
                <header>
                    <nav className="navbar navbar-expand-md navbar-dark bg-dark fixed-top">
                        <Link className="navbar-brand" to="/admin/home">
                            <span className="span-header">AZbot</span>
                        </Link>
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon" />
                        </button>
                        <div className="collapse navbar-collapse collapse-c" id="navbarsExampleDefault">
                            <ul className="navbar-nav mr-auto">
                                <li className="nav-item active">
                                    <Link className="nav-link" to="/admin/home">Home <span className="sr-only">(current)</span></Link>
                                </li>
                                <li className="nav-item dropdown">
                                    <a className="nav-link dropdown-toggle" href="/#" id="dropdown01" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Service</a>
                                    <div className="dropdown-menu" id="dropdown-menu-id" aria-labelledby="dropdown01">
                                        <Link className={this.state.linkDirect[0]} onClick={()=>{this.onDirectClick(0)}}  to="/admin/analysis">System Analysis</Link>
                                        <Link className={this.state.linkDirect[1]} onClick={()=>{this.onDirectClick(1)}} to="/admin/management-account">Account management</Link>
                                        <Link className={this.state.linkDirect[2]} onClick={()=>{this.onDirectClick(2)}} to="/admin/notification">Notification
                                        <span className="badge badge-danger noti-fi-num">10</span> </Link>
                                    </div>
                                </li>
                            </ul>
                            <ul className="navbar-nav">
                                <button onClick={this.logoutRequest} type="button" className="btn btn-outline-primary">
                                    <span><i className="fas fa-sign-out-alt" /></span>
                                        Sign out</button>
                            </ul>
                        </div>
                    </nav>
                </header>
                <div className="main-active">
                    <div className="container-fluid">
                        <Switch>
                            <Route path="/admin/analysis"><ADMAnalysis/></Route> 
                            <Route path="/admin/management-account"><ADMAccountMan/></Route>
                        </Switch>
                    </div>
                </div>
            </div>

        )
    }
}