import React from 'react'
import $ from 'jquery'
import "./admin-sys-acc-man.css"
import axios from 'axios'


export default class ADMAccountMan extends React.Component {
    constructor(props) {
        super(props)
        this.listInformation = []
        this.state = { 
            listSize: 0, 
            accState:{
                total:0,
                new:0,
                expire:0
            }
        }
        this.ToastRefs = React.createRef()
        this.showProfile = this.showProfile.bind(this)
        this.searchAccProfile = this.searchAccProfile.bind(this)
        this.initAccState = this.initAccState.bind(this)
        this.listSearchRefs = [React.createRef(), React.createRef(), React.createRef(), React.createRef()]
    }


    componentDidMount() {
        this.initAccState()
        this.initListAccount()
    }

    async initAccState(){
        let Value = await axios.post('/adm/get-sys-acc-state')
        this.setState({accState:Value.data})
    }

    async initListAccount() {
        let tempValue = await axios.post('/adm/get-full-account-to-render')
        this.listInformation = tempValue.data
        this.setState({ listSize: this.listInformation.length })
    }


    showProfile(obj) {
        $('#spinner-toasts').css('display','inline-block')
        axios.post('/adm/get-peer-profile', { username: obj }).then(res => {
            if (res !== null) {
                res = res.data
                $('#info-user-title').text(res.FullName);
                $('#toast-id-user').text(obj);
                $('#toast-level-user').text(res.Level);
                $('#toast-norma-user-his').text(res.hisLevel[0]);
                $('#toast-medi-user-his').text(res.hisLevel[1]);
                $('#toast-premi-user-his').text(res.hisLevel[2]);
                $('#toast-email-user').text(res.Email);
                $('#toast-contact-user').text(res.Contact);
                $('#toast-blanc-user').text(res.Balance);
                $('#toast-days-user').text(Math.ceil((res.CountDate) / (1000 * 60 * 60 * 24)));
                $('#toast-dayin-user').text(res.DateIn);
                $('#toast-state-user').text(res.State);
                $('#toast-state-user').attr('class', "text-" + res.classState)
                $('#spinner-toasts').css('display','none')
            }
        })
    }

    initCLickTracking = (selfID) => {
        this.showProfile(selfID)
        this.ToastRefs.current.style.display = 'inline-block'
        this.ToastRefs.current.style.opacity = 1
    }



    searchAccProfile() {
        let lstVal = this.listSearchRefs.map(e => e.current.value.toUpperCase())
        let tbl = document.getElementById('search-tab-bod-id');
        let listRows = tbl.getElementsByTagName("tr");
        for (let i = 0; i < listRows.length; i++) {
            let control = true
            for (let j = 0; j < 4; j++) {
                let td = listRows[i].getElementsByTagName("td")[j];
                if (td) {
                    let txtValue = td.textContent;
                    if (txtValue.toUpperCase().indexOf(lstVal[j]) === -1) {
                        listRows[i].style.display = "none";
                        control = false
                        break;
                    }
                }
            }
            if (control) listRows[i].style.display = "";
        }
    }

    hiddenToast(){
        this.ToastRefs.current.style.display = 'none'
        this.ToastRefs.current.style.opacity = 0
    }

    render() {
        let SpinnerValue = null
        if (this.state.listSize === 0) {
            SpinnerValue =
                <div className="spinnerData d-flex justify-content-center" >
                    <div className="spinner-border text-secondary" role="status">
                        <span className="sr-only">Loading...</span>
                    </div>
                </div>

        }
        return (
            <div>
                <div className="nav-body-active" style={{ paddingTop: '70px' }}>
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-xl-3">
                                <div style={{ marginTop: '10px' }}>
                                    <ul style={{ listStyle: 'none', width: '100%' }}>
                                        <li>
                                            <div className="alert alert-primary ana">Account: <span className="account-tot"> {this.state.accState.total} </span>
                                            </div>
                                        </li>
                                        <li>
                                            <div className="alert alert-success ana">New: +<span className="account-new"> {this.state.accState.new} </span></div>
                                        </li>
                                        <li>
                                            <div className="alert alert-danger ana">Overdue Payment: <span className="account-overd"> {this.state.accState.expire}</span>
                                            </div>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <div className="col-xl-8">
                                <div className="search-acc">
                                    <div className="search-bar mt-2 mb-2">
                                        <div className="input-group">
                                            <div className="input-group-append">
                                                <span type="button" className="btn btn-info">Search</span>
                                            </div>
                                            <input type="text" onChange={this.searchAccProfile} ref={this.listSearchRefs[0]} aria-label="Name" className="form-control" placeholder="Full Name" />
                                            <input type="text" onChange={this.searchAccProfile} ref={this.listSearchRefs[1]} aria-label="Contact" className="form-control" placeholder="Contact" />
                                            <input type="text" onChange={this.searchAccProfile} ref={this.listSearchRefs[2]} aria-label="Email" className="form-control" placeholder="Email" />
                                            <input type="text" onChange={this.searchAccProfile} ref={this.listSearchRefs[3]} aria-label="LastAccess" className="form-control" placeholder="LastAccess" />

                                        </div>
                                    </div>
                                    <div className="table-responsive" style={{ maxHeight: '80vh' }}>
                                        <table className="table " style={{ border: 'solid 1px #f5f5f5' }}>
                                            <thead className="thead-dark" id="search-tab-thead-id">
                                                <tr>
                                                    <th scope="col">Full name</th>
                                                    <th scope="col">Contact</th>
                                                    <th scope="col">Email</th>
                                                    <th scope="col">Last Access</th>
                                                </tr>
                                            </thead>
                                            <tbody className="search-tab-body" id="search-tab-bod-id">
                                                {this.listInformation.map(e => <PeerRow key={e._id} data={e} functionEvent={this.initCLickTracking} />
                                                )
                                                }
                                            </tbody>
                                        </table>
                                        {SpinnerValue}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div  ref={this.ToastRefs} className="toast-imple fixed-top" id="element" >
                            <div className="toasts-header">
                                <div id="spinner-toasts" className="spinner-grow text-info" role="status">
                                    <span className="sr-only">Loading...</span>
                                </div>
                                <strong className="mr-1">About : </strong><strong className="mr-auto" id="info-user-title"> </strong>
                                <button type="button" onClick={this.hiddenToast.bind(this)} className="ml-2 mb-1 close" >
                                    <span aria-hidden="true">×</span>
                                </button>
                            </div>
                            <div className="toasts-body">
                                <div className="d-flex align-items-center ml-2 mr-5  mb-1">
                                    <div className="mr-auto"><strong>ID: </strong> <span id="toast-id-user"></span></div>
                                    <div className="leveldisplay d-flex  align-items-center">
                                        <strong>Level: </strong>
                                        <span>
                                            <p className="btn mb-0 border-0 bg-white text-dark btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                <span id="toast-level-user"></span> </p>
                                            <div className="dropdown-menu drop-toast">
                                                <p className="dropdown-item"><strong>Normal:</strong> <span id="toast-norma-user-his">
                                                </span></p>
                                                <p className="dropdown-item"><strong>Medium:</strong> <span id="toast-medi-user-his">
                                                </span></p>
                                                <p className="dropdown-item"><strong>Premium:</strong> <span id="toast-premi-user-his">
                                                </span></p>
                                            </div>
                                        </span>
                                    </div>
                                </div>
                                <div className=" ml-2 mb-2"><strong>Email: </strong><span id="toast-email-user"></span></div>
                                <div className=" ml-2 mb-2"><strong>Contact: </strong><span id="toast-contact-user"></span>
                                </div>
                                <div className=" ml-2 mb-2"><strong>Balance: </strong><span id="toast-blanc-user"> </span><span className="text-monospace">$</span></div>
                                <div className="d-flex ml-2 mb-2 mr-5">
                                    <div className="mr-auto"><strong>Time:</strong> <span id="toast-days-user"> </span> days</div>
                                    <div> <strong>From:</strong> <span id="toast-dayin-user"></span></div>
                                </div>
                                <div className=" ml-2 mb-2"><strong>State:</strong> <span className="text-danger" id="toast-state-user"></span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}
class PeerRow extends React.Component {

    render() {
        return (
            <tr onClick={() => this.props.functionEvent(this.props.data._id)}>
                <td>{this.props.data.Fname + " " + this.props.data.Lname}</td>
                <td>{this.props.data.Contact}</td>
                <td>{this.props.data.Email}</td>
                <td>{this.props.data.LastAccess}</td>
            </tr>
        )
    }
}