import React from 'react'
import { Link, Switch, Route } from 'react-router-dom'
import FeatherIcons from 'feather-icons-react'
import "./admin-sys-analysis.css"
import AdminAccountAnalysis from "./modules/AdmAccAnalysis"

export default class ADMAnalysis extends React.Component {

    render() {
        return (
            <div>
                <div className="row">
                    <nav id="sidebarMenu" className="col-md-3 col-lg-2 d-md-block bg-light sidebar collapse">
                        <div className="sidebar-sticky pt-3">
                            <ul className="nav flex-column" style={{ position: 'fixed' }}>
                                <li className="nav-item">
                                    <Link className="nav-link active" to="/admin/analysis/account">
                                        <FeatherIcons icon={"users"} />
                  Account
                </Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/admin/analysis/device">
                                        <FeatherIcons icon={"cpu"} />
                  Device
                </Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/admin/analysis/products">
                                        <FeatherIcons icon={"shopping-cart"} />
                  Products
                </Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/admin/analysis/income">
                                        <FeatherIcons icon={"credit-card"} />
                  Income
                </Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/admin/analysis/reports">
                                        <FeatherIcons icon={"bar-chart-2"} />
                  Reports
                </Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/admin/analysis/Integrations">
                                        <FeatherIcons icon={"layers"} />
                  Integrations
                </Link>
                                </li>
                            </ul>
                        </div>
                    </nav>
                    <main role="main" className="col-md-9 ml-sm-auto col-lg-10 px-md-4">
                        <Switch>
                            <Route exact path="/admin/analysis/account"> <AdminAccountAnalysis/> </Route>
                        </Switch>

                    </main>
                </div>
            </div>
        )
    }
}